// Positionning
%center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

%backgroundCover {
    background-repeat: no-repeat;
    background-size: contain;
}


// Type
%uppercase {
    font-family: $sansSerif;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    font-weight: 500;
}