    .projects__item:nth-child(2n) {
      transform: translateY(20rem);
      animation: projectsFadeInEven 1.5s cubic-bezier(0.215, 0.61, 0.355, 1) forwards; }
    .projects__item:nth-child(1) {
      animation-delay: calc(.3s + 1s/5); }
    .projects__item:nth-child(2) {
      animation-delay: calc(.3s + 2s/5); }
    .projects__item:nth-child(3) {
      animation-delay: calc(.3s + 3s/5); }
    .projects__item:nth-child(4) {
      animation-delay: calc(.3s + 4s/5); }
