.transition__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  pointer-events: none; }

.transition__panels {
  width: calc(100% / 6);
  height: 100%;
  transform: scaleY(1);
  transform-origin: top center;
  background: #eef5ff;
  animation: transitionPanel 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) forwards; }
  body.js-pageInTransition .transition__panels {
    transform: scaleY(0);
    animation: transitionPanelReverse 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) forwards; }
    body.js-pageInTransition .transition__panels:nth-child(0) {
      animation-delay: calc(.0s + 0s/10); }
    body.js-pageInTransition .transition__panels:nth-child(1) {
      animation-delay: calc(.0s + 1s/10); }
    body.js-pageInTransition .transition__panels:nth-child(2) {
      animation-delay: calc(.0s + 2s/10); }
    body.js-pageInTransition .transition__panels:nth-child(3) {
      animation-delay: calc(.0s + 3s/10); }
    body.js-pageInTransition .transition__panels:nth-child(4) {
      animation-delay: calc(.0s + 4s/10); }
    body.js-pageInTransition .transition__panels:nth-child(5) {
      animation-delay: calc(.0s + 5s/10); }
    body.js-pageInTransition .transition__panels:nth-child(6) {
      animation-delay: calc(.0s + 6s/10); }
  .transition__panels:nth-child(0) {
    animation-delay: calc(.0s + 0s/10); }
  .transition__panels:nth-child(1) {
    animation-delay: calc(.0s + 1s/10); }
  .transition__panels:nth-child(2) {
    animation-delay: calc(.0s + 2s/10); }
  .transition__panels:nth-child(3) {
    animation-delay: calc(.0s + 3s/10); }
  .transition__panels:nth-child(4) {
    animation-delay: calc(.0s + 4s/10); }
  .transition__panels:nth-child(5) {
    animation-delay: calc(.0s + 5s/10); }
  .transition__panels:nth-child(6) {
    animation-delay: calc(.0s + 6s/10); }
  @media (max-width: 900px) {
    .transition__panels {
      display: none; } }

@keyframes transitionPanel {
  from {
    transform: scaleY(1); }
  to {
    transform: scaleY(0); } }

@keyframes transitionPanelReverse {
  from {
    transform: scaleY(0); }
  to {
    transform: scaleY(1); } }

